import logo from './logo.svg';
import Recommend from './views/recommend/index'
import ViewDocList from './views/viewDocList/index'
import Login from './views/login/index'
import Reg from './views/reg/index'
import Layout from './views/layout'
import './App.css';

import { HashRouter, Routes, Route, Navigate } from 'react-router-dom';
function App(){
    const LayoutRecommend = Layout(Recommend)
    const LayoutViewDocList = Layout(ViewDocList)

    return (
        <HashRouter>
            <Routes>
                <Route path='/recommend' element={<LayoutRecommend></LayoutRecommend>} />
                <Route path='/viewDocList' element={<LayoutViewDocList key={Math.random()}></LayoutViewDocList>} />
                <Route path='/login' element={<Login></Login>} />
                <Route path='/reg' element={<Reg></Reg>} />
                <Route path="*" element={<Navigate to="/viewDocList" />} />
            </Routes>
        </HashRouter>
    )
}

export default App;
