import { Form, Input, Button, Checkbox, message, Radio, Select, Modal,InputNumber } from 'antd';
import http from '@/service'
import { useNavigate } from 'react-router-dom'

import './index.scss'
import { useEffect,useState } from 'react';

const Login = (props) => {
  const [docList, setDocList] = useState([])
  const [addVisible, setAddVisible] = useState(false)
  const [moviesTypeList, setMoviesTypeList] = useState([])
  const navigate = useNavigate()
  useEffect(() => {
    getDocList()
  }, [])
  const onFinish = (values) => {
    values.like_movies = values.like_movies? values.like_movies.join(','):''
    console.log('Success:', values);
    if (values.like_age2 < values.like_age1) {
      message.warn('最大年龄不能小于最小年龄')
      return
    } else {
      values.like_age = `${values.like_age1}-${values.like_age2}`
    }
    if (values.password.length < 6) {
      message.warn('密码需大于6位')
      return
    }
    Reflect.deleteProperty(values, 'like_age1')
    Reflect.deleteProperty(values, 'like_age2')
    http({
      url: '/api/reg',
      method: 'POST',
      data: values
    }).then((res) => {
      console.log(res)
      if (res.code === 0) {
        message.success('Register Success')
        navigate('/login')
      }

    })
  };

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };
  const getDocList = () => {
    http({
      url: '/api/docList',
      method: 'POST'
    }).then((res) => {
      setDocList(res.value)
      setMoviesTypeList(Array.from(new Set(res.value.map(e=>e.genre).join(',').split(','))))
    })

  }

  return (
    <div className='login-container'>

      <Form
        name="basic"
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 16 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label="Username"
          name="username"
          rules={[{ required: true, message: 'Please input your username!' }]}
        >
          <Input style={{width: 165}} maxLength={16} />
        </Form.Item>

        <Form.Item
          label="Password"
          name="password"
          rules={[{ required: true, message: 'Please input your password!' }]}
        >
          <Input.Password style={{width: 165}} maxLength={10} />
        </Form.Item>
        <Form.Item
          label="sex"
          name="genre"
        >
          <Radio.Group>
            <Radio value={1}>woman</Radio>
            <Radio value={0}>man</Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item
          label="age"
          name="age"
        >
          <Select>
            {Array(82).fill(1).map((it,i) => {
              return <Select.Option value={(i+18).toString()} key={i}>{i+18}</Select.Option>
            })}
          </Select>
        </Form.Item>
        <Form.Item
          label="like movies type"
          name="like_movies"
        >
          <Select mode="multiple">
            {moviesTypeList.map((e) => <Select.Option value={e}>{e}</Select.Option>)}
          </Select>
        </Form.Item>
        <Form.Item
          label="find age"
          name="like_age1"
        >
          <InputNumber max={99} min={18} maxLength={2} placeholder='min' />
        </Form.Item>
        <Form.Item
          label="find age"
          name="like_age2"
        >
          <InputNumber max={99} min={18} maxLength={2} placeholder='max' />
        </Form.Item>


        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>

          <Button type="primary" htmlType="submit">
          Register
          </Button>
        </Form.Item>
      </Form>

    </div>
  );
};

export default Login;