import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Space, Table, Button, Input, message, DatePicker, List, Comment, Avatar, Modal, Form, Card, Tooltip, InputNumber,Upload,Select } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import { useNavigate, useSearchParams } from 'react-router-dom';
import http from '@/service'
import moment from 'moment'
import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default (props) => {

  const [filter, setFilter] = useState([() => true])
  const [moviesTypeList, setMoviesTypeList] = useState([])

  const [addVisible, setAddVisible] = useState(false)
  const [curr, setCurr] = useState(null)
  const [ratingVisible, setRatingVisible] = useState(false)
  const [info, setInfo] = useState({})
  const [docList, setDocList] = useState([])
  const [act, setAct] = useState('')
  const infoRef = useRef({})
  const navigate = useNavigate()
  const [searchParams] = useSearchParams()


  const onFinish = (values) => {
    values.poster = values.poster && values.poster.file && values.poster.file.response.value
    values.genre = values.genre && values.genre.join(',')
    if (Object.values(values).some((e) => !e)) {
      message.warning('Please complete')
      return
    }
    http({
      url: '/api/addDoc',
      method: 'POST',
      data: values
    }).then((res) => {
      setAct('')
      if (res.code === 0) {
        message.success(res.msg)
      }
      setAddVisible(false)
      getDocList()
    })
  }

  const onFinishRating = (values) => {
    http({
      url: '/api/addRating',
      method: 'POST',
      data: {
        rating: values.rating,
        username: info.username,
        title: curr.title
      }
    }).then((res) => {
      getDocList()
      setRatingVisible(false)
    })
  }


  const getDocList = () => {
    http({
      url: '/api/docList',
      method: 'POST'
    }).then((res) => {
      setDocList(res.value)
      setMoviesTypeList(Array.from(new Set(res.value.map(e=>e.genre).join(',').split(','))))

    })

  }

  const columns = [
    {
      title: 'Movie Name',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'Poster',
      dataIndex: 'poster',
      key: 'poster',
      render: (v) => <img style={{width: 80}} src={v}></img>

    },
    {
      title: 'date of release',
      dataIndex: 'date',
      key: 'date',
      render: (v) => v && moment(v).format('YYYY-MM-DD')
    },
    {
      title: 'rating',
      dataIndex: 'rating_avg',
      key: 'rating',
      render: (v) => v && Number.parseFloat(v).toFixed(1)
    },
    {
      title: 'director name',
      dataIndex: 'director',
      key: 'director',

    },
    {
      title: 'the list of genres',
      dataIndex: 'genre',
      key: 'genre',

    },
    {
      title: 'main cast actors',
      dataIndex: 'actor',
      key: 'actor',

    },
    {
      title: 'description',
      dataIndex: 'description',
      key: 'description',
      render: (v) => <Tooltip title={v}>{v && v.slice(0, 50) + '...'}</Tooltip>
    },



    {
      title: 'Option',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          <Button onClick={() => { setRatingVisible(true); setCurr(record) }}>rating</Button>
          {/* <Button onClick={() => handleDel(record)}>删除</Button> */}
        </Space>
      ),
    },
  ];


  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)
      infoRef.current = res.value
      getDocList()
    })

  }, [searchParams.get('lids')])


  return <div>
    <div className='home-box' style={{ width: '100%' }}>
      <Form
        name="search"
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 16 }}
        onFinish={() => {

        }}
        style={{ width: '100%' }}
        // onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label="Search With Title"
          name="title"
        >
          <Input onChange={(v) => {
            setFilter([(it) => v.target.value ? String(it.title).toLocaleLowerCase().includes(String(v.target.value).toLocaleLowerCase()) : true])
          }}></Input>
        </Form.Item>
        <Form.Item
          label="Search With Actors"
          name="actors"
        >
          <Input onChange={(v) => {
            setFilter([(it) => v.target.value ? String(it.actors).toLocaleLowerCase().includes(String(v.target.value).toLocaleLowerCase()) : true])
          }}></Input>
        </Form.Item>
        <Form.Item
          label="Search With Director"
          name="director"
        >
          <Input onChange={(v) => {
            setFilter([(it) => v.target.value ? String(it.director).toLocaleLowerCase().includes(String(v.target.value.toLocaleLowerCase())) : true])
          }}></Input>
        </Form.Item>


        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
          {/* <Button type="primary" htmlType="submit">
          search
        </Button> */}
          <Button style={{ margin: '10px 0' }} onClick={() => setAddVisible(true)}>Add Movie</Button>

        </Form.Item>
      </Form>
    </div>
    <Table columns={columns} dataSource={docList.filter(filter[0] || (() => true))}></Table>
    {addVisible && <Modal
      visible title="Add Movie"
      footer={false}
      width={600}
      onCancel={() => setAddVisible(false)}

    >
      <Form
        name="add"
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 16 }}
        onFinish={onFinish}
        // onFinishFailed={onFinishFailed}
        autoComplete="off"
      >

        <Form.Item
          label="Movie Name"
          name="title"
        >
          <Input></Input>
        </Form.Item>
        <Form.Item
          label="Poster"
          name="poster"
        >
          <Upload {...{
            name: 'file',
            action: '/api/upload',
            headers: {
              authorization: 'authorization-text',
            },
            multiple: false,
            maxCount: 1,
            onChange(info) {
              if (info.file.status !== 'uploading') {
                console.log(info.file, info.fileList);
              }

              if (info.file.status === 'done') {
                message.success(`${info.file.name} file uploaded successfully`);

              } else if (info.file.status === 'error') {
                message.error(`${info.file.name} file upload failed.`);
              }
            },
          }}>
            <Button>Click to Upload</Button>
          </Upload>
        </Form.Item>
        <Form.Item
          label="date of release"
          name="date"
        >
          <DatePicker></DatePicker>
        </Form.Item>
        <Form.Item
          label="director name"
          name="director"
        >
          <Input></Input>
        </Form.Item>
        <Form.Item
          label="the list of genres"
          name="genre"
        >
          <Select mode="multiple">
            {moviesTypeList.map((e) => <Select.Option value={e}>{e}</Select.Option>)}
          </Select>
        </Form.Item>
        <Form.Item
          label="main cast actors"
          name="actor"
        >
          <Input></Input>
        </Form.Item>
        <Form.Item
          label="description"
          name="description"
        >
          <Input></Input>
        </Form.Item>
        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>

          <Button type="primary" htmlType="submit">
            submit
          </Button>
        </Form.Item>
      </Form>
    </Modal>}
    {ratingVisible && <Modal
      visible title="Set Rating"
      onCancel={() => setRatingVisible(false)}
      footer={false}
    >
      <Form
        name="rating"
        labelCol={{ span: 6 }}
        wrapperCol={{ span: 16 }}
        onFinish={onFinishRating}
        // onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label="rating"
          name="rating"
        >
          <InputNumber min={0} max={10}></InputNumber>
        </Form.Item>
        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>

          <Button type="primary" htmlType="submit">
            submit
          </Button>
        </Form.Item>
      </Form>
    </Modal>}
  </div>
}